<template>
    <div class="Mycard">
        <div class="swiper-container Mycardbox">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(item,index) in card">
                    <strong class="num">
                        {{item.num}}
                    </strong>
                    <span class="name">{{item.name}}</span>
                </div>
            </div>
        </div>
        <section class="information">
            <h4>陈先生，您当前有4张房卡
                <span class="fr">{{activecard+1}}/{{card.length}}</span>
            </h4>
            <p>{{card[activecard].num}}房当前享有：2张早餐券；免押金；退房免查房；最晚 退房时间为次日15点之前。</p>
        </section>
        <div class="weui-grids">
            <a href="javascript:;" class="weui-grid" @click="Unlock=true">
                <div class="weui-grid__icon">
                    <img src="../assets/svg/Key.svg" alt="">
                </div>
                <p class="weui-grid__label">一键开锁</p>
            </a>
            <a href="javascript:;" class="weui-grid" @click="code=true">
                <div class="weui-grid__icon">
                    <img src="../assets/svg/breakfast.svg" alt="">
                </div>
                <p class="weui-grid__label">早餐券</p>
            </a>
            <a href="javascript:;" class="weui-grid" @click="dialog1=true">
                <div class="weui-grid__icon">
                    <img src="../assets/svg/See.svg" alt="">
                </div>
                <p class="weui-grid__label">费用明细</p>
            </a>
        </div>
        <div class="js_dialog" id="iosDialog" v-if="Unlock">
            <div class="weui-mask"></div>
            <div class="weui-dialog">
                <div class="weui-dialog__hd">
                    <strong class="weui-dialog__title">温馨提示</strong>
                </div>
                <div class="weui-dialog__bd">请确保在房门口进行开锁，是否确认开锁？</div>
                <div class="weui-dialog__ft">
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_default" @click="Unlock=flase">否</a>
                    <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary">是</a>
                </div>
            </div>
        </div>
        <Success v-if="isok" text="已开锁"></Success>
        <div class="code-box" v-if="code">
            <span class="Close" @click="code=false">关闭</span>
            <div class="code">
                <p class="title">向店员出示此二维码</p>
                <div class="swiper-container" id="code">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide">
                            <img src="../assets/images/room_7.png" alt="">
                            <p class="num">1</p>
                        </div>
                        <div class="swiper-slide">
                            <img src="../assets/images/room_7.png" alt="">
                            <p class="num">2</p>
                        </div>
                        <div class="swiper-slide">
                            <img src="../assets/images/room_7.png" alt="">
                            <p class="num">3</p>
                        </div>
                        <div class="swiper-slide">
                            <img src="../assets/images/room_7.png" alt="">
                            <p class="num">4</p>
                        </div>
                    </div>
                </div>
                <p class="Prompt">1008房当前有2张早餐券可用，左右滑动下一张</p>
                <p class="activeNum">1/2</p>
            </div>
        </div>
        <div class="js_dialog" v-if="dialog1">
            <div class="weui-mask" @click="dialog1=false"></div>
            <div class="weui-dialog Paymentdetails">
                <i class="weui-icon-cancel" @click="dialog1=false"></i>
                <div class="weui-dialog__hd">
                    费用明细
                </div>
                <div class="weui-dialog__bd">
                    <h4 class="title mt15">费用明细</h4>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                房费*1
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥1,338.00
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                押金*1
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥1,338.00
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                优惠券
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥1,338.00
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                积分
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥1,3
                            </div>
                        </div>
                    </div>
                    <div class="weui-flex">
                        <div class="weui-flex__item">
                            <div class="placeholder">
                                合计
                            </div>
                        </div>
                        <div class="weui-flex__item right">
                            <div class="placeholder">
                                +￥1,338.00
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script type='text/ecmascript-6'>
import Success from "@/components/Success"
export default {
    data() {
        return {
            Unlock: false,
            activecard: 0,
            isok: false,
            code: true,
            dialog1: false,
            quit: false,
            card: [
                {
                    "num": 120,
                    "name": "1多粉酒店房卡"
                },
                {
                    "num": 110,
                    "name": "2多粉酒店房卡"
                },
                {
                    "num": 999,
                    "name": "3多粉酒店房卡"
                },
                {
                    "num": 666,
                    "name": "4多粉酒店房卡"
                }
            ]
        }
    },
    mounted() {
        let _this = this;
        new Swiper('.Mycardbox', {
            // pagination: '.swiper-pagination', //盒子
            // slidesPerView: 'auto', //展示多少个
            // centeredSlides: true,  //设定为true时，活动块会居中，而不是默认状态下的居左。
            // // spaceBetween: 20, // slide之间的距离（单位px）。
            // width: 350,
            // // centeredSlides: true,
            slidesPerView: 'auto',
            centeredSlides: true, //居中
            paginationClickable: true, //点击分页器的指示点分页器会控制Swiper切换。
            spaceBetween: 10,
            loop: true,
            // autoplay: 1000,
            autoplayDisableOnInteraction: false,
            onSlideChangeStart: function(swiper) {
                _this.activecard = swiper.activeIndex % _this.card.length;
            }
        });
        var mySwiper = new Swiper('#code', {
            loop: true,
            onSlideChangeStart: function(swiper) {
                console.log(swiper.activeIndex % 3)
            }
        })
    },
    components: {
        Success
    }
}
</script>

<style scoped lang='less' rel='stylesheet/less'>

</style>



